import React, { Suspense, lazy } from "react";


const generateTilRoutes = (start: number, end: number) => {
    const route = [];
    for (let day = start; day <= end; day++) { // start  day 
        route.push({
            path: `/til/day-${day}`,
            day: String(day),
        });
    }
    return route; 
};

const routes = generateTilRoutes(1, 24); // <<------------||   ++ 

const loadComponent = (day: string) => {
    return lazy(() => import(`./til/Day${day}`)); //   
};

// routes        routeComponents  
const routeComponents = routes.map((route) => {
    return {
        path: route.path,
        component: loadComponent(route.day), //   
        day: route.day, // day  
    };
});

export default routeComponents;
