import Web3 from "web3";
import dotenv from "dotenv";


const INFURA_API_KEY = process.env.INFURA_API_KEY;

const web3 = new Web3(`https://public-en-kairos.node.kaia.io/v3/${ INFURA_API_KEY }`);

//  
export const getTransaction = async (txHash: string) => {
    try {
        const tx = await web3.eth.getTransaction(txHash);
        return tx;
    } catch (err) {
        console.error("  :", err);
        return null;
    }
};

//  
export const getBlock = async (blockNumber: string | number) => {
    try {
        const block = await web3.eth.getBlock(blockNumber);
        return block;
    } catch (err) {
        console.error("  :", err);
        return false;
    }
};

//   
export const getBalance = async (address: string) => {
    try {
        const balance = await web3.eth.getBalance(address);
        return balance;
    } catch (err) {
        console.error("   :", err);
        return null;
    }
}

export const getRecentTransactions = async (address:string) => {
    try {
        const transactions = await web3.eth.getBalance(address);
        return transactions;
    } catch (err) {
        console.error("    :", err);
        return null;
    }
}


