import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { getBalance, getRecentTransactions } from "../utils/web3";  //   

const bigIntReplacer = (key: string, value: any) => typeof value === "bigint" ? value.toString() : value;

const Address: React.FC = () => {
    const { address } = useParams<{ address?: string }>(); // URL  
    const [accountData, setAccountData] = useState<any | null>(null);
    const [error, setError] = useState<string | null>(null);
    const [loading, setLoading] = useState<boolean>(true); //   

    useEffect(() => {
        if (!address) {
            setError("  .");
            setLoading(false);
            return;
        }

        const fetchAccountData = async () => {
            try {
                setLoading(true);
                //   
                const balance = await getBalance(address);
                if (balance === null) {
                    setError("    .");
                    return;
                }
                //     
                const transactions = await getRecentTransactions( address );

                setAccountData({ balance, /**transactions*/ }); //    
            } catch (err) {
                setError("     .");
            } finally {
                setLoading(false);
            }
        };

        fetchAccountData();
    }, [address]);
    return (
        <div className="addressPage">
            <h1> </h1>
            {loading && <p> ...</p>}
            {error && <p className="error">{error}</p>}
            {accountData && (
                <div className="accountDetails">
                    <h3>{address}</h3>
                    <p>: {accountData.balance} wei</p>
                    <p>  : {JSON.stringify(accountData.transactions, bigIntReplacer, 2)}</p>
                </div>
            )}
        </div>
    );
};

export default Address;
