import "../css/Explorer.css";
import React, { useState } from "react";
import { getTransaction, getBlock } from "../utils/web3";
import { useNavigate } from "react-router-dom";

const bigIntReplacer = (key: string, value: any) => typeof value === "bigint" ? value.toString() : value;

const Explorer: React.FC = () => {
    const [searchQuery, setSearchQuery] = useState<string>("");
    const [result, setResult] = useState<any>(null);
    const [error, setError] = useState<string | null>(null);
    const navigate = useNavigate();

    //    
    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setSearchQuery(event.target.value);
        setError(null);
    };

    //  
    const handleSearch = async () => {
        setResult(null);
        setError(null);
        if (searchQuery.trim() === "") {
            setError(" .");
            return;
        }
        try {
            //  
            if (/^0x[a-fA-F0-9]{40}$/.test(searchQuery)) { // 16   
                navigate(`/explorer/address/${searchQuery}`); //   
                return;
            }
            //  
            const txResult = await getTransaction(searchQuery);
            if (txResult) {
                setResult(txResult);
                navigate(`tx/${searchQuery}`); //   
                return;
            }
            //  
            if (/^\d+$/.test(searchQuery)) {
                const blockResult = await getBlock(searchQuery);
                if (blockResult) {
                    navigate(`/explorer/block/${searchQuery}`)
                } else {
                    setError("   .");
                }
            } else {
                setError("  ,     .");
            }
        } catch (err) {
            setError("    .");
        }
    };

    return (
        <div className="pageWrap">
            <input
                className="searchBar"
                type="text"
                placeholder=" ,  ,   "
                value={searchQuery}
                onChange={handleChange}
                onKeyDown={(evt)=>{
                    if(evt.key === "Enter"){
                        handleSearch();
                    }
                }}
            />
            <button onClick={handleSearch}></button>
            
        </div>
    );
};

export default Explorer;
