import "../css/TxPage.css"
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { getTransaction } from "../utils/web3";
import Explorer from "./Explorer";

const bigIntReplacer = (key: string, value: any) => typeof value === "bigint" ? value.toString() : value;

const TxPage: React.FC = () => {
    const { txHash } = useParams<{ txHash?: string }>(); // URL   
    const [txData, setTxData] = useState<any | null>(null);
    const [error, setError] = useState<string | null>(null);
    const [loading, setLoading] = useState<boolean>(true); //   

    useEffect(() => {
        if (!txHash) {
            setError("   .");
            setLoading(false);
            return;
        }

        const fetchTransaction = async () => {
            try {
                setLoading(true);
                const tx = await getTransaction(txHash);
                if (tx) {
                    setTxData(tx); //   
                } else {
                    setError("   .");
                }
            } catch (err) {
                setError("    .");
            } finally {
                setLoading(false);
            }
        };

        fetchTransaction();
    }, [txHash]); // txHash   

    return (
        <div className="txPage">
            <Explorer />
            <h1> </h1>
            {loading && <p> ...</p>}
            {error && <p className="error">{error}</p>}
            {txData && (
                <div className="txDetails">
                    <h3> : {txHash}</h3>
                    <div className="txInfo">
                        <div><strong>  (From):</strong> {txData.from}</div>
                        <div><strong>  (To):</strong> {txData.to}</div>
                        <div><strong>  (Block Hash):</strong> {txData.blockHash}</div>
                        <div><strong>  (Block Number):</strong> {txData.blockNumber}</div>
                        <div><strong> (Gas):</strong> {txData.gas}</div>
                        <div><strong>  (Gas Price):</strong> {txData.gasPrice}</div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default TxPage;
