import "../css/BlockPage.css"
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { getBlock } from "../utils/web3";
import Explorer from "./Explorer";

const bigIntReplacer = (key: string, value: any) => typeof value === "bigint" ? value.toString() : value;

const BlockPage: React.FC = () => {
    const { blockNumber } = useParams<{ blockNumber?: string }>(); // URL   
    const [blockData, setBlockData] = useState<any | null>(null);
    const [error, setError] = useState<string | null>(null);
    const [loading, setLoading] = useState<boolean>(true); //   

    useEffect(() => {
        if (!blockNumber) {
            setError("   .");
            setLoading(false);
            return;
        }

        const fetchBlock = async () => {
            try {
                setLoading(true);
                const block = await getBlock(blockNumber);
                if (block) {
                    setBlockData(block); //   
                } else {
                    setError("   .");
                }
            } catch (err) {
                setError("    .");
            } finally {
                setLoading(false);
            }
        };

        fetchBlock();
    }, [blockNumber]); // blockNumber   

    return (
        <div className="blockPage">
            <h1> </h1>
            
            {loading && <p> ...</p>}
            {error && <p className="error">{error}</p>}
            {blockData && (
                <div className="blockDetails">
                    <h3> : {blockNumber}<Explorer/></h3>
                    <div className="blockInfo">
                        <div><strong> :</strong> {blockData.hash}</div>
                        <div><strong>Nonce:</strong> {blockData.nonce}</div>
                        <div><strong>:</strong> {blockData.miner}</div>
                        <div><strong>:</strong> {new Date(Number(blockData.timestamp) * 1000).toLocaleString()}</div>
                        <div><strong> :</strong> {blockData.gasUsed}</div>
                    </div>
                    <div className="transactions">
                        <h4> </h4>
                        {blockData.transactions && blockData.transactions.length > 0 ? (
                            <ul>
                                {blockData.transactions.map((tx: string, index: number) => (
                                    <li key={index}>
                                        <strong> {index + 1}:</strong>
                                        <div><strong> :</strong> {tx}</div>
                                    </li>
                                ))}
                            </ul>
                        ) : (
                            <p> .</p>
                        )}
                    </div>
                </div>
            )}
        </div>
    );
};

export default BlockPage;
